import {mapActions} from 'vuex'

export default {
  data () {
    return {
      nickName: this.$store.state.user.profile.nickName
    }
  },
  methods: {
    ...mapActions('user', ['updateUserInfo']),
    updateNickName () {
      this.updateUserInfo().then(res => {
        if (res) {
          this.$back()
        }
      })
    }
  }
}



